a {
  color: #000;
  text-decoration: none;
}

body {
  box-sizing: border-box;
  margin: 0 10px 10px;
}

.search-form {
  width: 100%;
  height: 60px;
  background-color: #f096eb;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
  position: fixed;
  right: 0;
}

.input {
  width: 280px;
  height: 36px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
}

.search-btn {
  cursor: pointer;
  height: 35px;
  background-color: #f3e1e1;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 320px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 0;
  display: grid;
}

.photo-card img {
  height: calc(100% - 70px);
  width: 100%;
  object-fit: cover;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

